@input-prefix-cls: ~'@{css-prefix}input';

.@{input-prefix-cls} {
  .input;
  &-wrapper {
    display: inline-block;
    width: 100%;
    position: relative;
    vertical-align: middle;

    // #2149 & #2219
    line-height: normal;
  }
  &-icon {
    width: 32px;
    height: @input-height-base;
    line-height: @input-height-base;
    font-size: 16px;
    text-align: center;
    color: @color-dark75;
    position: absolute;
    right: 0;
    z-index: 3;
  }
  &-hide-icon &-icon {
    display: none;
  }
  &-icon-validate {
    display: none;
  }

  &-icon-clear {
    display: none;
  }

  &-wrapper:hover {
    .@{input-prefix-cls}-icon-clear {
      display: inline-block;
    }
  }

  &-icon-normal + & {
    padding-right: 32px;
  }
  // #554
  &-hide-icon &-icon-normal + & {
    padding-right: @input-padding-horizontal;
  }

  &-wrapper-large &-icon {
    font-size: 18px;
    height: @input-height-large;
    line-height: @input-height-large;
  }
  &-wrapper-small &-icon {
    width: 24px;
    font-size: 14px;
    height: @input-height-small;
    line-height: @input-height-small;

    //+ .@{input-prefix-cls} {
    //    padding-right: 24px;
    //}
  }
}

.@{input-prefix-cls}-group {
  .input-group(~'@{input-prefix-cls}');
}

.@{form-item-prefix-cls}-error {
  .@{input-prefix-cls} {
    .input-error;
    &-icon {
      color: @color-error;
    }
  }
  .@{input-prefix-cls}-group {
    .input-group-error;
  }

  /* .@{transfer-prefix-cls} {
    .@{input-prefix-cls} {
      .input;
      &-icon {
        color: @color-dark75;
      }
    }
  } */
}
.@{form-item-prefix-cls}-validating {
  .@{input-prefix-cls} {
    &-icon-validate {
      display: inline-block;
    }
    &-icon + .@{input-prefix-cls} {
      padding-right: 32px;
    }
  }
}
